<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/entryestimasikanvas" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = F4/A4 <br> *Orientasi = Portrait <br> *Margin Browser = Default</p>
        <template id="template">
            <div class="grid-container">
                <table style="width:19.501cm;text-align:center;">
                    <tr>
                        <td>
                            <span style="font-size:14pt;" class="text-center"><b>DAFTAR BARANG YANG DIBAWA KANVAS</b></span>
                        </td>
                    </tr>
                </table>
                <table style="width:19.501cm;font-size:8pt;">
                    <tr >
                        <td style="width:9.7505cm;text-align:left;padding-left:3cm;">Tanggal : {{ this.karton["Tanggal"] }}</td>
                        <td style="width:9.7505cm;text-align:left;padding-left:3cm;">Daerah Kanvas : {{ this.karton["DaerahKanvas"]}}</td>
                    </tr>
                    <tr>
                        <td style="text-align:left;padding-left:3cm;">No Kanvas : {{ this.karton["NoKanvas"]}}</td>
                        <td style="text-align:left;padding-left:3cm;">Nama Sales : {{ this.karton["NamaSales"]}}</td>
                    </tr>
                </table>
                <table id="tabel" style="font-size:8pt;height:0.4cm;" >
                    <tr style="text-align:center; font-weight:bold;">
                        <td id="td">Kode Barang</td>
                        <td id="td">Jumlah Dibawa</td>
                        <td id="td">Pencacahan Barang Yang Laku</td>
                        <td id="td">Jumlah Laku</td>
                        <td id="td">Jumlah Kembali</td>
                    </tr>
                    <tr v-for="i in this.karton" :key="i.index" style="height: 0.6cm">
                        <td id="td" style="width:2cm;padding-left:0.2cm;">{{ i["KodeBarang"]}}</td>
                        <td id="td" style="width:2.501cm;padding-left:0.2cm;">{{ i["JmlBrgDibawa"] | currency }}</td>
                        <td id="td" style="width:10cm;"></td>
                        <td id="td" style="width:2.501cm"></td>
                        <td id="td" style="width:2.501cm"></td>
                    </tr>
                </table>
                <table style="width:19.501cm;font-size:8pt;margin-top:0.3cm;">
                    <tr>
                        <td style="width:9.7505cm;text-align:left; padding-left:1cm;">Bagian Gudang</td>
                        <td style="width:9.7505cm;text-align:right; padding-right:1.2cm;">Salesman</td>
                    </tr>
                    <br><br>
                    <tr>
                        <td style="width:9.7505cm;text-align:left; padding-left:0.69cm;">_____________________</td>
                        <td style="width:9.7505cm;text-align:right; padding-right:0.4cm;">_____________________</td>
                    </tr>
                </table>
            </div>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                karton: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'kanvass/datalaporanbarangdibawa'
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                    this.karton["Tanggal"] = response.data[0]["Tanggal"]
                    this.karton["NoKanvas"] = response.data[0]["NoKanvas"]
                    this.karton["DaerahKanvas"] = response.data[0]["DaerahKanvas"]
                    this.karton["NamaSales"] = response.data[0]["NamaSales"]
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }

    #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
    }

</style>